import React, {useState, Component, useEffect} from 'react';
import s from './NumbPuzzle.module.css';


let PopUp = ({moveCounts, selectNewGame}) => {
    return (
        <div className={s.popup}>
            <div>

                <div className={s.textPopUp}>
                     : {moveCounts}<br/>
                    <span> :</span>
                    <button className={s.btnYes} onClick={() => selectNewGame(true)}></button>
                    <button className={s.btnNo} onClick={() => selectNewGame(false)}></button>
                </div>

            </div>

        </div>
    )
}



export default PopUp;