import React, {Component} from 'react'
import s from './ToDoList.module.css';
import {Field,reset, reduxForm} from "redux-form";
import {Input} from "../common/FormsControls/FormsControls";
import {v4} from 'uuid'

let Form = (props) => {
    return <form onSubmit={props.handleSubmit}>
        <Field name="NewItem" component={Input} type = 'text' placeholder=" " value = 'dgdg'/>
        <button onClick={()=>reset}></button>
    </form>
}


const afterSubmit = (result, dispatch) =>
    dispatch(reset('todo'));

Form = reduxForm({form: "todo", onSubmitSuccess: afterSubmit})(Form);





class ToDoItem extends Component {
    constructor(props) {
        super(props);

    }

    delete(key) {
        this.props.delete(key);
    }

    render() {
        let {entries} = this.props;
        return (
            <ul className={s.theList}>
                {entries.map((item) =>
                    <li onClick={() => this.delete(item.key)} key={item.id}> {item.text} </li>
                )}
            </ul>
        )
    }
}


class ToDoList extends Component {
    constructor() {
        super();
        this.state = {
            items: [
                {text: '', key: v4()}
                ],
            value:''
        };
        this.addItem = this.addItem.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
    }

    addItem(text) {
        let itemArray = this.state.items;
        console.log(itemArray)
        if (text.NewItem !== undefined) {
            itemArray.unshift({
                text: text.NewItem,
                key: v4()
            })
            console.log(itemArray)
            this.setState({
                items: itemArray
            });
        }
    }

    deleteItem(key) {
        console.log(key)
        let deleteItems = this.state.items.filter(item => item.key !== key);
        console.log(deleteItems)
        this.setState({
            items: deleteItems
        });
    }

    render() {
        let {items} = this.state
        console.log(items)
        return (
            <div className={s.todoListMain}>
                <div className={s.header}>
                    <Form onSubmit={this.addItem}  className = {s.myForm}/>
                </div>
                <ToDoItem entries={items}
                          delete={this.deleteItem}
                />
            </div>

        )

    }
}

let ToDoListContainer = () => <div className={s.container}>
    <ToDoList/>
</div>

export default ToDoListContainer;


