import React from 'react';
import {connect} from 'react-redux';
import {compose} from "redux";

import News from "./News";
import {addNew} from "../../redux/news-reducer";

class NewsContainer extends React.Component {
    render() {
        return <News
            {...this.props}
            news={this.props.news} //  mapStateToProps state c  news
        />
    }
}

//   mapStateToProps    Reducer  newsState
let mapStateToProps = (state) => {
    return {
        news: state.newsPage.news, //   news  Reducer - "news-reducer.js"   newsPage(     redux-store.js)    
    }
}

export default compose(
    connect(mapStateToProps,{addNew}) //  mapStateToProps    Reducer("news-reducer.js")     {addNew, ..., ...,...},    ,   State      <Provider />
)(NewsContainer)
